.menu{
    font-family: system-ui, -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
}

.menu .container{
    display: flex;
    flex-direction: column;
    align-items: center;
}


.menu-container{
    display: grid;
    grid-template-columns: 1fr 1fr;
    column-gap: 4rem;
    padding: 2rem 0;
}


.menu-page{
    display: none;
}

.active{
    display: block;

}

.menu-btn{
    padding: 1rem;
    border: 1px solid #e9ecef;
    color: black;
    background-color: white;
    letter-spacing: .2rem;
    font-size: .9rem;
    font-weight: 300;
    margin: .5rem;
    font-family: system-ui, -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
}

.btn-active{
    border: 1px solid black;
}



.menu h2{
    text-align: center;
    color: black;
}

.menu-item{
    padding: 1rem 0;
}

.menu-item h3 {
    padding: .5rem 0;;
    color: #212529;
    font-weight: 300;
    font-size: 18px;
}

.menu-item h3 span{
    float: right;

}

.menu-item p{
    color: grey;
    font-weight: 300;
    font-size: 1rem;
}


@media screen and (min-width: 768px) and (max-width: 990px) {
    .menu-container{
        grid-template-columns: 1fr;
    }
}

/* TABLET */

@media screen and (min-width: 576px) and (max-width: 768px) {
  .menu-container{
        grid-template-columns: 1fr;
    }
}

/* MOBILE */

@media screen and (max-width: 576px) {
  .menu-container{
        grid-template-columns: 1fr;
    }

    .menu-btn{
        padding:.5rem;
    }
}