.hero{
    width: 100%;
    height: 100vh;
    position: relative;
    background-image: url('../../assets/hero.webp');
    background-size: cover;
    background-position: 50% 50%;
    background-repeat: no-repeat;
}

.hero .container{
    position: relative;
}

.overlay{
    background-color: rgba(0, 0, 0, 0.2);
    position: absolute;
    width: 100%;
    height: 100%;
}

.hero-box{
    display: flex;
    height: 100vh;
    width: 100%;

}

#hero-box_left{
    width: 50%;
    height: 100%;
    background-image: url('../../assets/hero.jpg');
    background-repeat: no-repeat;
    background-size: cover;
    background-position: 50%;
    background-attachment: fixed;
    
}

#hero-box_left img{
    height: 100%;
    width: 100%;
    object-fit: cover;
}

#hero-box_right{
    width: 50%;
    background-color: red;
}

.hero .content{
    position: absolute;
    top: 0;
    height: 100%;
    display: flex;
    justify-content: space-evenly;
    align-items: center;
    flex-direction: column;
    width: 100%;
    margin: auto;
    
}

.hero .content #res-btn{
    position:absolute;
    top: 70%;
}

.res-btn{
    font-size: 1rem;
    padding: 1rem 2rem;
    color: white;
    background-color: black;
    font-family: system-ui, -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
    border: 1px solid white;
}

.res-btn:hover{
    background-color: white;
    color: black;
}


.hero .content #chevron{
    position:absolute;
    top: 90%;
}

h1{
    font-family: 'Hiro';
    font-size: 10rem;
    position: absolute;
    top: 32%;
}



.hero-btn{
    transition: .2s ease-in-out;
}

.hero-btn:hover{
    transform: scale(1.5);
}

.carousel img {
    height: 100vh;
    object-fit: cover;
}

.control-dots{
    display: none;
}

.control-arrow{
    display: none;
}

.thumbs{
    display: none;
}

.slider li{
    padding: 0;
}



@media screen and (max-width: 576px) {
    h1{
        font-size: 8rem;
    }
}
