.contact{
    font-family: system-ui, -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
    background-color: #0d0d0e;
    
}

.contact-container{
    display: grid;
    grid-template-columns: repeat(4, 1fr);
    column-gap: 4rem;
    padding: 8rem 0;
}


.contact .container h2{
    text-align: center;
    color: white;
    padding: 2rem 0;
}





.contact p{
    color: white;
    padding: .3rem 0;
    font-weight: 300;
    font-size: 1rem;
}

.contact h3{
    color: white;
    font-size: 1.4rem;
    font-family: var(--primary-font);
    font-weight: 600;
    letter-spacing: .1rem;
    margin-bottom: 1rem;
}

.form-box{
    display: flex;
    flex-direction: column;
    grid-column: 1/3;
}

.open-hours{
    text-align: center;
}

.form-box h3{
    margin-bottom: 0;
}

.form-box p{
    letter-spacing: 0.2rem;
    margin-bottom: 2rem;
}

form{
    display: flex;
    flex-direction: column;
}

label{
    color: white;
}

form input{
    width: 100%;
    padding: .5rem;
    margin: .5rem 0;
    border: 0;
    border-bottom: 1px solid white;
    background: transparent;
    color: white;
}


form input[type=text]:focus{
    outline: none;
}
.form-btn{
    padding: .5rem;
    margin: .5rem 0;
    margin-top: 1rem;
    background-color: transparent;
    border-style: none;
    border: 1px solid white;
    color: white;
}
.contact-social{
    display: flex;
    padding: .3rem 0;
}
.contact-icon{
    color: white;
    padding: 0 0.3rem;
    font-size: 1.8rem;
}

.contact-icon:hover{
    transform: scale(1.5);
    transition: 0.3s;
}

@media screen and (min-width: 768px) and (max-width: 990px) {
    .contact-container{
        grid-template-columns: 1fr;
        grid-gap: 4rem;
        text-align: center;
        padding: 8rem 0 0 0;
    }

    .contact-social{
    display: flex;
    justify-content: center;
    padding: 2rem 0;
}


    .contact-icon{
        padding: 0 0.8rem;
        font-size: 3.5rem;
    }

    .form-box{
        grid-column: 1;
    }

    #email::placeholder{
        text-align: center;
    }
}

/* TABLET */

@media screen and (min-width: 576px) and (max-width: 768px) {
