:root{
  --primary: #c9ab81;
  --primary-dark: #0b1315;
  --primary-light: #0f1d22;
  --primary-font: system-ui, -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
}


@font-face {
  font-family: 'Hiro';
  src: local('HIROMISAKE'),
  url('./fonts/HIROMISAKE.ttf') format('truetype');

}



*{
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

ul{
  list-style-type: none;
}

a{
  text-decoration: none;
}

h1{
  font-size:3.5rem
}

h2{
  font-size: 2rem;
  font-weight: 300;
  letter-spacing: .5rem;
  padding: 2rem 0;
}

button{
  cursor: pointer;
  outline: none;
}

.sect-pad{
  padding: 6rem 0;
}

.container{
  max-width: 1440px; /*use instead of padding left and right   980px forthis project*/
  margin: 0 auto;
  padding: 0 10rem;
 
}


@media screen and (min-width: 990px) and (max-width: 1440px) {
  .container , .menu-container{
    max-width: 960px;
    padding: 0 1rem;
  }
}

/* TABLET */

@media screen and (min-width: 768px) and (max-width: 990px) {
    .container , .mobile-container{
      max-width: 720px;
      padding: 0 1rem;
    }
}

/* TABLET */

@media screen and (min-width: 576px) and (max-width: 768px) {
  .container , .mobile-container{
    max-width: 540px;
    padding: 0 1rem;
  }
}

/* MOBILE */

@media screen and (max-width: 576px) {
  .container , .mobile-container{
    width: 100%;
    padding: 1rem;
  }

  .sect-pad{
    padding: 1rem 0;
  }
  
}