.reservation{
    height: auto;
    width: 100%;
    padding: 10rem 0;
    background-color: black;
    /*background-image: url('../../assets/resbg.jpg');*/
    background-repeat: no-repeat;
    background-size: cover;
    background-attachment: fixed;
    color: white;

    display: flex;
}

.reservation .container{
    width: 100%;
}

.reservation h2{
    text-align: center;
    font-family: var(--primary-font);
    padding: 0 0 2rem 0;
}

.res-form{
    display: flex;
    flex-direction: row;
    justify-content: space-between;
}

.res-form > *{
    width: 24%;
    margin:0;
    padding: 1rem;
    color-scheme: dark;
}

.res-form_btn{
    background-color: black;
    border: 1px solid white;
    color: white;
}

#party{
    width: 24%;
    border: 0;
    border-bottom: 1px solid white;
    background: black;
    color: white;
}


@media screen and (min-width: 768px) and (max-width: 990px) {
    .reservation{
        height: auto;
    }
    .res-form{
        flex-direction: column;
    }

    .res-form > *, #party{
    width: 100%;
    text-align: center;
    margin:0;
    padding: 1rem;
    margin-bottom: 3rem;
}
}

