html, body{
    scroll-behavior: smooth;
}


.navbar h2{
    font-family: Hiro;
    padding: 0;
}

.navbar{
    width: 100%;
    height: 80px;
    background-color: rgba(0,0,0,0.9);
    text-decoration: none;
    z-index: 2;
    position: sticky;
    margin-top: -80px;
    top: 0;

    transition: top .3s;

    
    font-family: system-ui, -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
}

.navbar .container{
    height: 100%;

    display: flex;
    justify-content: space-between;
    align-items: center;

}




.navbar-bg{
    transform: translateY(-80px);  
}



.hamburger{
    display: none;
}

.icon{
    font-size: 1.5rem;
}

.nav-menu{
    display: flex;
}

li{
    padding: 0 1rem;
}

.nav-link{
    display: inline-block;
    color: #fff;
    letter-spacing: .2rem;
}

.nav-link::after{
    content: '';
    display: block;
    width: 0;
    height: 2px;
    background: #fff;
    transition: width .3s;
}

.nav-link:hover::after{
    width: 100%;
    transition: width .3s;
}

.block{
    width: 4rem;
}

.mobile-menu{
    display: none;
    text-align: center;
    height: 100vh;
}


@media screen and (min-width: 768px) and (max-width: 990px) {
    .nav-menu, .block{
        display: none;
    }

    .mobile-container{
        display: flex;
        flex-direction: column;
        width: 100%;
    }

    .mobile-menu{
        display: flex;
        flex-direction: column;
        width: 100%;
       /* color: rgba(255, 255, 255, .95); */
        position: absolute;
        top: 80px;
        left: -100vw;
        justify-content: space-between;
        align-items: center; 
        background-color: #f5f5f5; 
        color: #000;
        transition: 0.3s;
    }

    .dark{
        color:#000;
    }

    .active{
        left: 0px;
    }

    .mobile-menu li {
        padding: 1.2rem 1rem;
        font-size: 1.5rem;

    }

    .mobile-nav {
        margin: 1rem 0;
    }
    
    .mobile-menu-bottom{
        width: 100%;
        text-align: center;
        padding: 1rem 0;
    }

    .social-icons{
        display: flex;
        justify-content: space-evenly;
        margin: 2rem 0;
    }

    .social-icons .icon:hover{
        transform: scale(1.5);
        transition: 0.3s;
    }
    
    .hamburger{
        display: block;
    }       
}

