.about{
    font-family: system-ui, -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
}

.about .container{
    display: grid;
    grid-template-columns: repeat(3,1fr);
    column-gap: 2rem;

}


.container-box{
    display: flex;
}

.right-about{
    color: #868e96;
    padding: 0 2rem;

    display: flex;
    flex-direction: column;
    align-items: center;
    
}

.about h2{
    color: black;
    text-align: left;
    width: 100%;
    padding: 0;
    
}

.about h3{
    color: rgb(196, 0, 0);
    text-align: left;
    width: 100%;
    font-weight: 300;

    display: flex;
    align-items: center;
}

.about h3:hover {
    color: rgb(255, 0, 0);
}

.about img{
    height: 100%;
    width: 100%;
    object-fit: cover;
}

.right-about p {
    text-align: left;
    font-size: 1rem;
    padding: 1.5rem 0;
    line-height: 1.8;
    font-weight: 300;
}

@media screen and (min-width: 768px) and (max-width: 990px) {

    .about .container{
        grid-template-columns: repeat(2,1fr);
        row-gap: 4rem;
    }

    .right-about{
        padding: 0;
        grid-column: span 2;
    }
}

@media screen and (min-width: 576px) and (max-width: 768px) {
    .about .container{
        grid-template-columns: repeat(2,1fr);
        row-gap: 4rem;
    }

    .right-about{
        padding: 0;
        grid-column: span 2;
    }
  }
  
  @media screen and (max-width: 576px) {
    .about .container{
        grid-template-columns: repeat(2,1fr);
        row-gap: 2rem;
        column-gap: 1rem;
    }

    .right-about{
        padding: 0;
        grid-column: span 2;
    }

    .right-about p {
        padding: 1rem 0;
    }
  }